'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';
import '../css/problem.css';


var appBasePath=globalData.appBasePath;
var Problem=React.createClass({
	getInitialState:function(){
		return {
		}
	},
	
	componentWillMount:function(){
		
	},
	
	toNewsDetail:function(){
		var data = {id:3,name:"qin",age:18};
		var path = {
		  pathname:'/NewsDetail',
		  query:data,
		}
		hashHistory.push(path);
	},
	
	toAsk:function(){
		var data = {id:3,name:"qin",age:18};
		var path = {
		  pathname:'/Ask',
		  query:data,
		}
		hashHistory.push(path);
	},
	render:function(){
		var that=this;
		//console.log("cityId",cityId);
		
        return (
        	<div className="app_Box problem">
      			<Header title=""/>
	        	<div className="problemCon content">
	        		<div className="problemList">
        					<div className="problemBlock">
        						<img src="src/img/icon/problem.png"/>
        						<p>,4000,5,</p>
        						<span>:2017-09-28</span>
        					</div>
        					<div className="answerBlock">
        						<img src="src/img/icon/answer.png"/>
        						<p><span></span><span>2017-09-28</span></p>
        						<p></p>
        					</div>
        				</div>
        			<div className="problemList">
        					<div className="problemBlock">
        						<img src="src/img/icon/problem.png"/>
        						<p>,4000,5,</p>
        						<span>:2017-09-28</span>
        					</div>
        					<div className="answerBlock">
        						<img src="src/img/icon/answer.png"/>
        						<p><span></span><span>2017-09-28</span></p>
        						<p></p>
        					</div>
        			</div>
        			<div className="askBtn" onClick={this.toAsk}></div>
	        	</div>
	        	
        	</div>
        )
	}
});


export default Problem;





