'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory} from 'react-router';

var toast=new Toast();
var ForgotPsd=React.createClass({
	getInitialState:function(){
		return {
			getMsg:{
				style:{
						backgroundColor:"#ffa81e",
						color:"#ffffff"
					},
				getMsgTxt:"",
				disabled:false
			},
		}
	},
	changeMsgTxt:function(e){
		this.setState({
			getMsg: {
				getMsgTxt: e.target.value
			}
		})
	},
	getMsg:function(){
		var that=this;
		//inputdisablereadonlyonClickiOSonTouchEndAndroidHackiosonTouchEndandroidonClick
		let phoneNum=that.state.phoneNum;
		if(!(/^1[34578]\d{9}$/.test(phoneNum))){
			toast.show("",2000);
		}else{
			setTimeout(function(){
				var time=11;
				var timeevt=setInterval(function(){
						time--;
						that.setState({
							getMsg:{
								style:{
										backgroundColor:"#aaaaaa",
										color:"#ffffff"
									},
								getMsgTxt:time+"s",
								disabled:true
							}
						})
						
						if(time==0){
							clearInterval(timeevt);
							that.setState({
								getMsg:{
									style:{
											backgroundColor:"#ffa81e",
											color:"#ffffff"
										},
								getMsgTxt:"",
								disabled:false
								}
							})
						}
					},1000)
			},1000);
		}
	},
	vauleChange:function(e){
		this.setState({
			[e.target.name]:  e.target.value
		})
	},
	yzHandle:function(){
		var that=this;
		let phoneNum=that.state.phoneNum;
		let yzCode=that.state.yzCode;
		var toast = new Toast();
		if(phoneNum&&yzCode){
			let path = {
				  pathname:'/Repsd'
				}
				hashHistory.push(path);
			toast.show("",2000);
		}else{
			toast.show("",2000);
		}
	},
	render:function(){
		var that=this;
		let getMsgStyle=that.state.getMsg.style;
		let display=that.state.display;
		let getMsgTxt=that.state.getMsg.getMsgTxt;
		let disabled=that.state.getMsg.disabled;
		let changeMsgTxt=that.state.changeMsgTxt;
		//var phoneNum=this.props.location.state.phoneNum;
       return (
        	<div className="forgotPsd app_Box">
        		<Header title="" />
        		<div className="forgotPsdCon">
        			<div className="inputPsd">
    					<label htmlFor="phoneNum"></label>
    					<input id="phoneNum"  type="number"  name="phoneNum" placeholder="" onChange={that.vauleChange}/>
    				</div>
    				
    				<div className="inputPsd">
    					<label htmlFor="yzCode"></label>
    					<input id="yzCode" className="shortInput"  type="text"  name="yzCode" placeholder="" onChange={that.vauleChange}/>
    					<input type="text" onClick={that.getMsg} placeholder="" readOnly="readOnly" disabled={disabled} style={getMsgStyle} className="getMsg" id="getMsg"  value={getMsgTxt} onChange={that.changeMsgTxt}/>
    				</div>
    				<div className="psdLogin" onClick={that.yzHandle}></div>
        		</div>
        	</div>
        )
	}
	
});


export default ForgotPsd;





